import React from "react";
import { connect } from "react-redux";
import * as ScheduleActions from "../../store/Schedule/scheduleAction";
import { bindActionCreators } from "redux";
import { withRouter } from "react-router-dom";
import "./invites.scss";
import SearchInput from "../../components/Atoms/CustomizedInputs/SearchInput";
import searchIcon from "../../assets/images/Icon feather-search.svg";
import CustomSelect from "../../components/Atoms/customSelect/customSelect";
import Checkbox from "@material-ui/core/Checkbox";
import { Card, CardContent, IconButton, Chip } from "@material-ui/core";
import ArrowRightIcon from "@mui/icons-material/ArrowRight";
import TableComponent from "../../components/Controls/Table/sortableTable";
import WhatsappIcon from "../../assets/images/schedule/WhatsappIcon.svg";
import SmsIcon from "../../assets/images/schedule/SmsIcon.svg";
import GmailIcon from "../../assets/images/schedule/GmailIcon.svg";
import MailIcon from "@mui/icons-material/Mail";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import Button from "../../components/Controls/Button/Button";
import MessagePopup from "./MessagePopup";
import EmailPopup from "./EmailPopup";
import Navbar from "../../components/Navbar/Navbar";
import MessageIcon from "@mui/icons-material/Message";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import SendIcon from "@mui/icons-material/Send";
import Modal from "@material-ui/core/Modal";
import CloseCommentIcon from "../../assets/images/Icon ionic-ios-close-circle.svg";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";
import ReactGA from "react-ga";
import DatePicker from "../../components/DatePicker/Datepicker";
import TimePicker from "../../components/DatePicker/TimePicker";
import TimezoneSelect from "react-timezone-select";
import * as JDActions from "../../store/JobDescription/JobDescriptionAction";
import moment from "moment";
import JoditEditor from "jodit-react";
import HistoryIcon from "@mui/icons-material/History";
const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#fff",
    color: "#000000",
    fontSize: 12,
    fontWeight: "600",
  },
}));
let queryParams = new URLSearchParams(window.location.search);
const config = {
  readonly: false,
  height: 300,
  width: "100%",
};
class Invites extends React.Component {
  constructor(props) {
    super(props);
    this.editor = React.createRef(null);
    this.state = {
      search: "",
      page: 1,
      limit: 10,
      statusOptions: [
        { label: "Sent", value: "SENT" },
        { label: "Delivered", value: "DELIVERED" },
        { label: "Opened", value: "OPENED" },
        { label: "Clicked", value: "CLICKED" },
        { label: "Bounced", value: "BOUNCED" },
        { label: "Appearing", value: "APPEARING" },
        { label: "Submitted", value: "FINISHED" },
        { label: "Evaluated", value: "PROCCESSED" },
        { label: "Shortlisted", value: "SHORTLISTED" },
        { label: "Rejected", value: "REJECTED" },
        { label: "Next round", value: "NEXT_ROUND" },
        { label: "Offer released", value: "OFFER_RELEASED" },
        { label: "Negotiation", value: "NEGOTIATION" },
        { label: "Company joined", value: "COMAPNY_JOINED" },
        { label: "Offer Accepted", value: "OFFER_ACCEPTED" },
      ],
      jdOptions: [],
      jdSeleted: [],
      scheduleOptions: [],
      scheduleSeleted: [],
      statusSeleted: [],
      responseOptions: [
        { label: "All", value: "All" },
        { label: "Recieved", value: true },
        { label: "Not Recieved", value: false },
      ],
      responseSelected: "",
      whatsappOptions: [
        { label: "All", value: "All" },
        { label: "Enabled", value: true },
        { label: "Disabled", value: false },
      ],
      whatsappSelected: "",
      seledtedRowId: [],
      totalPage: 1,
      tableValue: [],
      selectedPages: [],
      selected: false,
      jobTitle: "",
      startDate: "",
      endDate: "",
      experience: "",
      currentPhone: "",
      currentMessage: "",
      whatsappPopup: false,
      emailPopup: false,
      currentEmail: [],
      emailSubject: "",
      smsPopup: false,
      selectedEmailPhone: [],
      label: "",
      inviteId: "",
      whatsappTemplateModal: false,
      whatsAppTemplate: null,
      templateCustom: [],
      templateOptions: [],
      templateSelected: "",
      profile: [],
      whatsappEvents: [],
      moreActionMulti: [
        { label: "Send SMS", value: "sms" },
        { label: "Send Email", value: "email" },
        { label: "Reschedule", value: "reschedule" },
      ],
      acionMultiSelected: null,
      rescheduleModal: false,
      startDateReschedule: moment(),
      endDateReschedule: moment().add(7, "d"),
      timefrom: moment(new Date())
        .hours(0)
        .minutes(0)
        .seconds(0)
        .milliseconds(0),
      timeTo: moment(new Date()).hours(0).minutes(0).seconds(0).milliseconds(0),
      timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      content: "",
      rescheduleClicked: false,
      allowedDevices: "ALL_DEVICE",
      allowedDevicesOptions: [
        { value: "LAPTOP", label: "Only PC" },
        { value: "MOBILE", label: "Only Mobile" },
        { value: "ALL_DEVICE", label: "Both PC And Mobile" },
      ],

      reminderCount: 0,
      rescheduleCount: 0,
      reminderAndRescheeduleOptions: [
        { value: 0, label: "0" },
        { value: 1, label: "1" },
        { value: 2, label: "2" },
        { value: 3, label: "3" },
      ],
    };
  }

  async componentDidMount() {
    this.props.ScheduleAction.getTemplates();

    this.props.JDAction.getJobDescriptionData();
    this.props.ScheduleAction.getScheduleData({});
    // if (window.location.pathname.split("invites/")[1]) {
    ReactGA.pageview(`${window.location.pathname.split("invites/")[0]}invites`);
    // let scheduleId = window.location.pathname.split("invites/")[1];
    let queryParams = new URLSearchParams(window.location.search);

    if (queryParams.get("search")) {
      let search = queryParams.get("search");
      if (search) {
        await this.setState({
          search: search,
        });
      }
    }
    if (queryParams.get("page")) {
      let page = queryParams.get("page");
      if (page) {
        await this.setState({
          page: parseInt(page),
        });
      }
    }
    if (queryParams.get("limit")) {
      let limit = queryParams.get("limit");
      if (limit) {
        await this.setState({
          limit: parseInt(limit),
        });
      }
    }

    if (queryParams.get("status")) {
      let status = queryParams.get("status");
      let statusArray = status.split(",");
      let newStatus = [];
      this.state.statusOptions.map((item) => {
        if (statusArray.includes(item.value)) {
          newStatus.push(item);
        }
      });

      await this.setState({
        statusSelected: newStatus,
      });
    }

    if (queryParams.get("whatsapp")) {
      let Whatsapp = queryParams.get("whatsapp");

      this.state.whatsappOptions.map(async (item) => {
        if (item.value.toString() == Whatsapp) {
          await this.setState({
            whatsappSelected: item,
          });
        }
      });
    }

    if (queryParams.get("response")) {
      let Response = queryParams.get("response");

      this.state.responseOptions.map(async (item) => {
        if (item.value.toString() == Response) {
          await this.setState({
            responseSelected: item,
          });
        }
      });
    }

    if (queryParams.get("reschedule")) {
      let Reschedule = queryParams.get("reschedule");

      this.state.reminderAndRescheeduleOptions.map(async (item) => {
        if (item.value.toString() == Reschedule) {
          await this.setState({
            rescheduleCount: item,
          });
        }
      });
    }

    if (queryParams.get("reminder")) {
      let Reminder = queryParams.get("reminder");

      this.state.reminderAndRescheeduleOptions.map(async (item) => {
        if (item.value.toString() == Reminder) {
          await this.setState({
            reminderCount: item,
          });
        }
      });
    }

    // this.setState(
    //   {
    //     scheduleId: scheduleId,
    //   },
    //   this.reuseApiCall
    // );
    // } else {
    this.reuseApiCall();
    // }
  }

  async componentDidUpdate(prevProps) {
    const curJd = this.props && this.props.JDReducer;
    const prevJd = prevProps && prevProps.JDReducer;

    if (
      prevJd.getJobDescriptionData !== curJd.getJobDescriptionData &&
      curJd.getJobDescriptionData &&
      curJd.getJobDescriptionData.length > 0
    ) {
      const options = curJd.getJobDescriptionData.map((el) => ({
        label: el.title + " " + el.minExp + "-" + el.maxExp,
        value: el.id,
      }));
      this.setState({ jdOptions: options });

      if (queryParams.get("JDs")) {
        let JD = queryParams.get("JDs");
        let JDArray = JD.split(",");
        let newJD = [];
        options.map((item) => {
          if (JDArray.includes(item.value)) {
            newJD.push(item);
          }
        });
        await this.setState({
          jdSeleted: newJD,
        });
        this.reuseApiCall();
      }
    }
    const cur = this.props && this.props.ScheduleReducer;
    const prev = prevProps && prevProps.ScheduleReducer;
    if (
      prev.getScheduleData !== cur.getScheduleData &&
      cur.getScheduleData &&
      cur.getScheduleData.data &&
      cur.getScheduleData.data.results &&
      cur.getScheduleData.data.results.length > 0
    ) {
      const options = cur.getScheduleData.data.results.map((el) => {
        return {
          value: el._id,
          label:
            (el && el.jdId && el.jdId.title) +
            "-" +
            (el &&
              el.fromTimestamp &&
              new Date(el.fromTimestamp).toLocaleDateString()) +
            ", " +
            (el && el.candidates && el.candidates.length) +
            " Candidates",
        };
      });

      if (queryParams.get("schedule")) {
        let Schedule = queryParams.get("schedule");
        let ScheduleArray = Schedule.split(",");
        let newSchedule = [];
        options.map((item) => {
          if (ScheduleArray.includes(item.value)) {
            newSchedule.push(item);
          }
        });
        await this.setState({
          scheduleSeleted: newSchedule,
        });

        this.reuseApiCall();
      }

      this.setState({
        scheduleOptions: options,
      });
    }

    if (
      prev.invitesDataArrived != cur.invitesDataArrived &&
      cur.invitesDataArrived &&
      cur.invites
    ) {
      let results = cur.invites && cur.invites.data && cur.invites.data.results;
      let totalPage =
        cur.invites && cur.invites.data && cur.invites.data.totalPages;
      let tableData = [];
      let jdIdDetails = results && results[0] && results[0].jdId;
      if (jdIdDetails) {
        let jobTitle = jdIdDetails && jdIdDetails.title;
        let startDate =
          jdIdDetails &&
          jdIdDetails.startDate.substring(
            0,
            jdIdDetails.startDate.indexOf("T")
          );
        let endDate =
          jdIdDetails &&
          jdIdDetails.endDate.substring(0, jdIdDetails.endDate.indexOf("T"));
        let experience =
          jdIdDetails &&
          `${jdIdDetails.minExp}years To ${jdIdDetails.maxExp}years`;

        localStorage.setItem(
          "scheduleDesc",
          `${jobTitle} : ${experience} | ${startDate} To ${endDate}`
        );
        this.setState({
          jobTitle: jobTitle,
          startDate: startDate,
          endDate: endDate,
          experience: experience,
          label: `${jobTitle} : ${experience} | ${startDate} To ${endDate}`,
        });
      }

      results &&
        results.map((item, index) => {
          tableData.push({
            startdate: `${
              index + (this.state.page - 1) * this.state.limit + 1
            }. ${item.createdAt.substring(0, item.createdAt.indexOf("T"))}`,
            name: item.candidateId && item.candidateId.name,
            email: item.candidateId && item.candidateId.email,
            phone: item.candidateId && item.candidateId.phone,
            isError: item.isError,
            device:
              item.scheduleId &&
              item.scheduleId.modeOfTest &&
              item.scheduleId.modeOfTest == "ALL_DEVICE"
                ? "All Device"
                : item.scheduleId.modeOfTest == "LAPTOP"
                ? "PC"
                : "Mobile",
            trackStatus: item.status,
            interaction: item.lastInteraction,
            id: item._id,
            data: item,
            jdCount: item.jdCount,
            scheduleCount: item.scheduleCount,
            whatsappEnabled: item.whatsappEnabled,
            whatsappSeen: item.whatsappSeen,
            reminderCount: item.reminderCount,
            rescheduleCount: item.rescheduleCount,
            candidateId: item && item.candidateId && item.candidateId._id,
          });
        });

      this.setState({
        tableValue: tableData,
        totalPage: totalPage,
      });
    }

    if (cur.isMessagePosted != prev.isMessagePosted && cur.isMessagePosted) {
      this.setState(
        {
          currentMessage: "",
          currentPhone: "",
          whatsappPopup: false,
          smsPopup: false,
        },
        this.reuseApiCall()
      );
    }
    if (cur.isEmailPosted != prev.isEmailPosted && cur.isEmailPosted) {
      this.setState(
        {
          currentMessage: "",
          currentEmail: [],
          emailPopup: false,
          emailSubject: "",
        },
        this.reuseApiCall()
      );
    }
    if (
      prev.templatesDataArrived != cur.templatesDataArrived &&
      cur.templatesDataArrived &&
      cur.templates
    ) {
      if (
        cur.templates &&
        cur.templates.data &&
        cur.templates.data.messageTemplates
      ) {
        let templateOptions = [];
        cur.templates.data.messageTemplates.map((item) => {
          templateOptions.push({
            label: item.elementName,
            value: item.id,
            data: item,
          });
        });

        this.setState({
          templateOptions: templateOptions,
        });
      }
    }

    if (
      prev.inviteHistory !== cur.inviteHistory &&
      cur.inviteHistory &&
      cur.inviteHistory.data &&
      cur.isInviteHistory
    ) {
      let whatsappEvents = [];
      cur.inviteHistory.data.events.map((item) => {
        if (item.type === "whatsapp") {
          whatsappEvents.push(item);
        }
      });
      whatsappEvents.sort(function (a, b) {
        return new Date(a.timestamp) - new Date(b.timestamp);
      });
      this.setState({
        whatsappPopup: true,
        whatsappEvents: whatsappEvents,
      });
    }
    if (
      prev.isTemplateMessagePosted !== cur.isTemplateMessagePosted &&
      cur.isTemplateMessagePosted
    ) {
      this.setState({
        whatsappTemplateModal: false,
        whatsAppTemplate: null,
        templateCustom: [],
        // templateOptions: [],
        templateSelected: "",
        profile: [],
        whatsappEvents: [],
      });
    }
    if (
      prev.reschedule !== cur.reschedule &&
      cur.reschedule &&
      cur.isReschedule
    ) {
      this.setState(
        {
          rescheduleModal: false,
          startDateReschedule: moment(),
          endDateReschedule: moment().add(7, "d"),
          timefrom: moment(new Date())
            .hours(0)
            .minutes(0)
            .seconds(0)
            .milliseconds(0),
          timeTo: moment(new Date())
            .hours(0)
            .minutes(0)
            .seconds(0)
            .milliseconds(0),
          timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
          content: "",
          rescheduleClicked: false,
          allowedDevices: "ALL_DEVICE",
        },
        this.reuseApiCall()
      );
    }
  }

  reuseApiCall = () => {
    let { jdSeleted, scheduleSeleted } = this.state;
    let jdIds = "",
      scheduleIds = "";
    jdSeleted &&
      jdSeleted.length > 0 &&
      jdSeleted.map((el, index) =>
        index === 0 ? (jdIds = el.value) : (jdIds = jdIds + "," + el.value)
      );

    scheduleSeleted &&
      scheduleSeleted.length > 0 &&
      scheduleSeleted.map((el, index) =>
        index === 0
          ? (scheduleIds = el.value)
          : (scheduleIds = scheduleIds + "," + el.value)
      );
    let status = [];
    this.state.statusSeleted.map((item) => {
      status.push(item.value);
    });

    let payload = {
      jdId: jdIds,
      scheduleId: scheduleIds ? scheduleIds : "",
      page: this.state.page,
      limit: this.state.limit,
      search: this.state.search,
      status: status ? status : null,
      responseId:
        this.state.responseSelected.value === true ||
        this.state.responseSelected.value === false
          ? this.state.responseSelected.value
          : null,
      whatsappEnabled:
        this.state.whatsappSelected.value === true ||
        this.state.whatsappSelected.value === false
          ? this.state.whatsappSelected.value
          : null,
      reminderCount: this.state.reminderCount && this.state.reminderCount.value,
      rescheduleCount:
        this.state.rescheduleCount && this.state.rescheduleCount.value,
    };

    this.props.ScheduleAction.getInvites(payload);

    // this.setState({
    //   selectedPage: "",
    //   selectedPages: [],
    //   seledtedRowId: [],
    //   selectedEmailPhone: [],
    // });
  };

  selectStatus = (e) => {
    let status = [];
    e.map((item) => {
      status.push(item.value);
    });
    queryParams.set("status", status);
    queryParams.set("page", 1);
    window.history.replaceState(null, null, "?" + queryParams.toString());

    this.setState({ statusSeleted: e, page: 1 }, this.reuseApiCall);
  };

  handlePagination = (e) => {
    queryParams.set(
      "page",
      e > 0 && e <= this.state.totalPage ? e : this.state.page
    );
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        page: e > 0 && e <= this.state.totalPage ? e : this.state.page,
      },
      () => {
        this.reuseApiCall();
      }
    );
  };

  setLimit = async (e) => {
    queryParams.set("limit", e);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        limit: e,
        page: 1,
      },
      () => {
        this.reuseApiCall();
      }
    );
  };

  handleBackPagination = async () => {
    if (this.state.page !== 1) {
      queryParams.set("page", this.state.page - 1);
      window.history.replaceState(null, null, "?" + queryParams.toString());
      await this.setState({ page: this.state.page - 1 }, () => {
        this.reuseApiCall();
      });
    }
  };
  handleNextPagination = async () => {
    if (this.state.page !== this.state.totalPage) {
      queryParams.set("page", this.state.page + 1);
      window.history.replaceState(null, null, "?" + queryParams.toString());
      await this.setState({ page: this.state.page + 1 }, () => {
        this.reuseApiCall();
      });
    }
  };

  handleSelectRow = async (e, val) => {
    if (e.target.checked) {
      await this.setState({
        selected: true,
        seledtedRowId: [...this.state.seledtedRowId, val.original.id],
        selectedEmailPhone: [
          ...this.state.selectedEmailPhone,
          {
            id: val.original.id,
            email: val.original.email,
            phone: val.original.phone,
            whatsappEnabled: val.original.whatsappEnabled,
            name: val.original.name,
          },
        ],
      });
      let selectedPages = this.state.selectedPages;
      if (this.state.seledtedRowId.length === this.state.tableValue.length) {
        selectedPages.push(this.state.page);
        this.setState({
          selectedPages: selectedPages,
        });
      }
    } else {
      let selectedPages = this.state.selectedPages;
      selectedPages = selectedPages.filter((item) => item !== this.state.page);
      await this.setState({
        selected:
          this.state.seledtedRowId.filter((x) => x !== val.original.id)
            .length === 0
            ? false
            : true,
        seledtedRowId: this.state.seledtedRowId.filter(
          (x) => x !== val.original.id
        ),
        selectedPages: selectedPages,
        selectedEmailPhone: this.state.selectedEmailPhone.filter(
          (x) => x.id !== val.original.id
        ),
      });
    }
  };

  handleSelectAll = async (e, val) => {
    if (!val) {
      return;
    }
    const { selectedPages, page } = this.state;
    let selectedPage = selectedPages;
    if (e.target.checked) {
      selectedPage.push(page);
      let selectedRowId = this.state.seledtedRowId;
      let selectedEmailPhone = this.state.selectedEmailPhone;
      let newSelected = val.map((x) => x.id);
      let newSelectedEmailPhone = [];
      val.map(async (x) => {
        await newSelectedEmailPhone.push({
          id: x.id,
          phone: x.phone,
          email: x.email,
          whatsappEnabled: x.whatsappEnabled,
          name: x.name,
        });
      });

      let uniq = [...new Set(selectedRowId.concat(newSelected))];

      let uniq2 = selectedEmailPhone.concat(newSelectedEmailPhone);

      uniq2 = uniq2.filter((value, index) => {
        const _value = JSON.stringify(value);
        return (
          index ===
          uniq2.findIndex((obj) => {
            return JSON.stringify(obj) === _value;
          })
        );
      });

      await this.setState({
        selectedPages: selectedPage,
        selected: true,
        seledtedRowId: uniq,
        selectedEmailPhone: uniq2,
      });
    } else {
      let newVal = val.map((item) => item.id);
      let NewselectedPages = this.state.selectedPages.filter((item) => {
        return item != this.state.page;
      });
      let difference = this.state.seledtedRowId.filter((x) => {
        return !newVal.includes(x);
      });

      let difference2 = this.state.selectedEmailPhone.filter((x) => {
        return !newVal.includes(x.id);
      });
      await this.setState({
        selected: false,
        seledtedRowId: difference,
        selectedPages: NewselectedPages,
        selectedEmailPhone: difference2,
      });
    }
  };

  handleKeyPress = (event) => {
    if (event.key === "Enter") {
      this.setState({ page: 1 }, () => {
        if (this.state.search) {
          this.reuseApiCall();
        }
      });
    }
  };
  handleSearch = (e) => {
    queryParams.set("search", e.target.value);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState({ search: e.target.value, page: 1 }, () => {
      if (e.target.value.length >= 3) {
        this.reuseApiCall();
      }
      if (e.target.value === "") {
        this.reuseApiCall();
      }
    });
  };
  handleSearchIcon = (val) => {
    this.setState({ page: 1 }, this.reuseApiCall);
  };
  HistoryPage = (data) => {
    // localStorage.setItem("InviteHistoryData", data && data.name);
    this.props.history.push({
      pathname: `/history`,
      search: `?inviteId=${data && data.id}&candidateId=${
        data && data.candidateId
      }`,
      state: data,
    });
  };

  sendWhatsapp = () => {
    if (this.state.currentPhone.split(",").length == 1) {
      this.props.ScheduleAction.createMessage({
        to: this.state.currentPhone,
        medium: "whatsapp",
        body: this.state.currentMessage,
        inviteId: this.state.inviteId
          ? this.state.inviteId
          : this.state.seledtedRowId[0],
      });
    } else {
      let inviteId = "";
      this.state.seledtedRowId.map((item, index) => {
        if (index != this.state.seledtedRowId.length - 1) {
          inviteId += item + ",";
        } else {
          inviteId += item;
        }
      });
      this.props.ScheduleAction.createMessage({
        to: this.state.currentPhone,
        medium: "whatsapp",
        body: this.state.currentMessage,
        bulk: true,
        inviteId: inviteId,
      });
    }
  };

  sendSMS = () => {
    if (this.state.currentPhone.split(",").length == 1) {
      this.props.ScheduleAction.createMessage({
        to: this.state.currentPhone,
        medium: "sms",
        body: this.state.currentMessage,
        inviteId: this.state.inviteId,
      });
    } else {
      let inviteId = "";
      this.state.seledtedRowId.map((item, index) => {
        if (index != this.state.seledtedRowId.length - 1) {
          inviteId += item + ",";
        } else {
          inviteId += item;
        }
      });
      this.props.ScheduleAction.createMessage({
        to: this.state.currentPhone,
        medium: "sms",
        body: this.state.currentMessage,
        bulk: true,
        inviteId: inviteId,
      });
    }
  };

  sendEmail = () => {
    let currentEmail = "";
    this.state.currentEmail.map((item, index) => {
      if (index != this.state.currentEmail.length - 1) {
        currentEmail += item + ",";
      } else {
        currentEmail += item;
      }
    });

    if (this.state.currentEmail.length == 1) {
      this.props.ScheduleAction.createEmail({
        to: currentEmail,
        subject: this.state.emailSubject,
        body: this.state.currentMessage,
        inviteId: this.state.inviteId,
      });
    } else {
      let inviteId = "";
      this.state.seledtedRowId.map((item, index) => {
        if (index != this.state.seledtedRowId.length - 1) {
          inviteId += item + ",";
        } else {
          inviteId += item;
        }
      });
      this.props.ScheduleAction.createEmail({
        to: currentEmail,
        subject: this.state.emailSubject,
        body: this.state.currentMessage,
        inviteId: inviteId,
        bulk: true,
      });
    }
  };
  renderValues = (value, getTagProps) => {
    // optionsValue = value;
    let newVals = [];
    value &&
      value.map((val) => {
        let i = val.split(",");
        i.length === 1 ? newVals.push(i[0]) : i.map((el) => newVals.push(el));
      });

    if (newVals.length == 1) {
      return newVals.map((option, index) => (
        <Chip variant='outlined' label={option} {...getTagProps({ index })} />
      ));
    } else {
      return (
        <div style={{ display: "flex", alignItems: "center" }}>
          <Chip variant='outlined' label={newVals[0]} {...getTagProps(0)} />
          <div style={{ fontSize: "11px", marginLeft: "5px" }}>
            +{newVals.length - 1}
          </div>
        </div>
      );
    }
  };

  onChangeCurrentEmail = (value) => {
    // this.setState(
    //   {
    //     currentEmail: value,
    //   },
    //   () => {
    //     if (!value.length) {
    //       this.setState({
    //         emailPopup: false,
    //       });
    //     }
    //   }
    // );
  };

  sendViaEmail = () => {
    let selectedEmails = [];

    this.state.selectedEmailPhone.map((item) => {
      selectedEmails.push(item.email);
    });

    this.setState({
      currentEmail: selectedEmails,
      emailPopup: true,
    });
  };

  sendViaWhatsapp = async (e) => {
    await this.setState({
      templateSelected: e,
    });

    let selectedPhone = [];

    this.state.selectedEmailPhone.map((item) => {
      if (item.whatsappEnabled) {
        selectedPhone.push(item.phone);
      }
    });

    let currentPhone = "";
    selectedPhone.map((item, index) => {
      if (index != selectedPhone.length - 1) {
        currentPhone += item + ",";
      } else {
        currentPhone += item;
      }
    });
    await this.setState({
      whatsAppTemplate: e.data,
      templateCustom: e.data.customParams,
    });

    this.setState({
      whatsappTemplateModal: true,
      currentPhone: currentPhone,
    });
  };

  sendViaSMS = () => {
    let selectedPhone = [];

    this.state.selectedEmailPhone.map((item) => {
      selectedPhone.push(item.phone);
    });

    let currentPhone = "";
    selectedPhone.map((item, index) => {
      if (index != selectedPhone.length - 1) {
        currentPhone += item + ",";
      } else {
        currentPhone += item;
      }
    });

    this.setState({
      smsPopup: true,
      currentPhone: currentPhone,
    });
  };

  selectResponse = (e) => {
    queryParams.set("response", e.value);
    queryParams.set("page", 1);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        responseSelected: e,
        page: 1,
      },
      this.reuseApiCall
    );
  };

  selectWhatsapp = (e) => {
    queryParams.set("whatsapp", e.value);
    queryParams.set("page", 1);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        whatsappSelected: e,
        page: 1,
      },
      this.reuseApiCall
    );
  };

  selectReminder = (e) => {
    queryParams.set("reminder", e.value);
    queryParams.set("page", 1);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        reminderCount: e,
        page: 1,
      },
      this.reuseApiCall
    );
  };

  selectReschedule = (e) => {
    queryParams.set("reschedule", e.value);
    queryParams.set("page", 1);
    window.history.replaceState(null, null, "?" + queryParams.toString());
    this.setState(
      {
        rescheduleCount: e,
        page: 1,
      },
      this.reuseApiCall
    );
  };
  profileData = (e, item) => {
    const profile = this.state.profile;
    const key = item && item.paramName.toLowerCase();
    const val = e.target.value;
    let data = profile.findIndex((el) => el[key]);

    if (data !== -1) {
      let newData = profile[data];
      newData[key] = val;
      profile[data] = newData;
    } else {
      let newData = {};
      newData[key] = val;
      profile.push(newData);
    }

    this.setState({ profile });
  };
  sendTemplate = () => {
    const {
      selectedEmailPhone,
      whatsAppTemplate,
      templateCustom,
      profile,
      jobTitle,
    } = this.state;

    const list = [];
    let inviteIds = "";
    selectedEmailPhone &&
      selectedEmailPhone.length > 0 &&
      selectedEmailPhone.map((item, ind) => {
        if (item.whatsappEnabled) {
          inviteIds +=
            item.id && ind < selectedEmailPhone.length - 1
              ? item.id + ","
              : item.id;
        }
        if (item.whatsappEnabled) {
          let data = {
            whatsappNumber: `91${item.phone}`,
            customParams: [],
          };
          templateCustom &&
            templateCustom.length > 0 &&
            templateCustom.map((val) => {
              const newVal = profile.find(
                (el) => el[val && val.paramName.toLowerCase()]
              );

              data.customParams.push({
                name: val && val.paramName.toLowerCase(),
                value:
                  val &&
                  val.paramName.toLowerCase() !== "name" &&
                  newVal &&
                  newVal[val && val.paramName.toLowerCase()]
                    ? newVal[val && val.paramName.toLowerCase()]
                    : val && val.paramName.toLowerCase() === "name"
                    ? item.name
                    : "",
              });
            });
          list.push(data);
        }
      });

    const payload = {
      template_name: whatsAppTemplate && whatsAppTemplate.elementName,
      broadcast_name: jobTitle,
      receivers: list,
      inviteId: inviteIds,
      text: whatsAppTemplate && whatsAppTemplate.body,
    };

    this.props.ScheduleAction.sendTemplateMessage(payload);
  };
  moreActionHandler = (e) => {
    if (e.value === "sms") {
      this.sendViaSMS();
    } else if (e.value === "email") {
      this.sendViaEmail();
    } else if (e.value === "reschedule") {
      // this.rescheduleHandler();
      this.setState({ rescheduleModal: true });
    }
  };
  validate = () => {
    let validate = true;
    if (this.state.content === "") {
      validate = false;
    }
    if (this.state.timeZone === "") {
      validate = false;
    }
    return validate;
  };
  rescheduleHandler = () => {
    const {
      startDateReschedule,
      endDateReschedule,
      timeTo,
      timeZone,
      timefrom,
      content,
      allowedDevices,
      seledtedRowId,
    } = this.state;
    this.setState({ rescheduleClicked: true });
    if (this.validate()) {
      const payload = {
        scheduleMessage: content,
        rescheduleDateFrom:
          startDateReschedule && timefrom
            ? `${this.GetFormattedDate(new Date(startDateReschedule))} ${moment(
                new Date(timefrom)
              ).format("HH:mm:ss")}`
            : null,
        rescheduleDateTo:
          endDateReschedule && timeTo
            ? `${this.GetFormattedDate(new Date(endDateReschedule))} ${moment(
                new Date(timeTo)
              ).format("HH:mm:ss")}`
            : null,
        modeOfTest: allowedDevices,
        timezone: timeZone && timeZone.value ? timeZone.value : timeZone,
        inviteIds: seledtedRowId,
      };

      this.props.ScheduleAction.reschedule(payload);
    }
  };
  GetFormattedDate = (date) => {
    return (
      (date.getMonth() > 8
        ? date.getMonth() + 1
        : "0" + (date.getMonth() + 1)) +
      "/" +
      (date.getDate() > 9 ? date.getDate() : "0" + date.getDate()) +
      "/" +
      date.getFullYear()
    );
  };
  handleUpdate = (event) => {
    const editorContent = event;
    let parser = new DOMParser();

    this.setState({
      content: editorContent,
    });
  };
  viewHistory = (data) => {
    this.props.history.push({
      pathname: `/history`,
      search: `?candidateId=${data && data.candidateId}`,
      state: data,
    });
  };

  selectJds = (e) => {
    let JDs = [];
    e.map((item) => {
      JDs.push(item.value);
    });
    queryParams.set("JDs", JDs);
    window.history.replaceState(null, null, "?" + queryParams.toString());

    this.setState({ jdSeleted: e, page: 1 }, () => {
      let jdIds = "";
      this.state.jdSeleted &&
        this.state.jdSeleted.length > 0 &&
        this.state.jdSeleted.map((el, index) =>
          index === 0 ? (jdIds = el.value) : (jdIds = jdIds + "," + el.value)
        );
      // this.props.ScheduleAction.getScheduleData({ jdId: jdIds });
    });
  };

  selectSchedule = (e) => {
    let schedule = [];

    e.map((item) => {
      schedule.push(item.value);
    });
    queryParams.set("schedule", schedule);
    window.history.replaceState(null, null, "?" + queryParams.toString());

    this.setState({ scheduleSeleted: e, page: 1 }, this.reuseApiCall);
  };
  render() {
    const {
      search,
      page,
      limit,
      jdOptions,
      jdSeleted,
      scheduleOptions,
      scheduleSeleted,
      statusOptions,
      statusSeleted,
      tableValue,
      totalPage,
      seledtedRowId,
      currentMessage,
      currentPhone,
      currentEmail,
      emailSubject,
      responseOptions,
      responseSelected,
      whatsappOptions,
      whatsappSelected,
      templateCustom,
      whatsAppTemplate,
      templateOptions,
      templateSelected,
      selectedEmailPhone,
      profile,
      moreActionMulti,
      acionMultiSelected,
      rescheduleClicked,
      reminderAndRescheeduleOptions,
      rescheduleCount,
      reminderCount,
    } = this.state;
    let preview =
      whatsAppTemplate && whatsAppTemplate.body ? whatsAppTemplate.body : "";
    let optionsLength =
      templateSelected.data &&
      templateSelected.data.customParams &&
      templateSelected.data.customParams.length;
    if (
      selectedEmailPhone &&
      selectedEmailPhone[0] &&
      selectedEmailPhone[0].name
    ) {
      preview = preview.replace("{{1}}", selectedEmailPhone[0].name);
    }

    for (let i = 2; i < optionsLength + 1; i++) {
      if (profile && profile[i - 2]) {
        preview = preview.replace(`{{${i}}}`, Object.values(profile[i - 2]));
      }
    }

    let inboundDatacolumn = [
      {
        Header: (
          <div>
            <Checkbox
              checked={this.state.selectedPages.includes(this.state.page)}
              onChange={(e) => this.handleSelectAll(e, tableValue)}
            />
          </div>
        ),
        width: 50,
        fixed: "left",
        accessor: "shortlisted",
        sortable: false,
        Cell: (val) => (
          <IconButton className='short-iconBtn'>
            <Checkbox
              checked={
                this.state.seledtedRowId.includes(val.original.id)
                  ? true
                  : false
              }
              onChange={(e) => this.handleSelectRow(e, val)}
              name='selected'
            />
          </IconButton>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Date</div>,
        minWidth: 80,
        accessor: "startdate",
        fixed: "left",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Name</div>,
        minWidth: 150,
        accessor: "name",
        fixed: "left",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              display: "flex",
              textAlign: "center",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
              <div  onClick={() => this.viewHistory(row.original)}>
            {row && row.value && row.value.length <= 12 ? (
              row.value
            ) : (
              <LightTooltip title={row.value} placement='top'>
                <div>{ row.value.substring(0,12)+'...'}</div>
              </LightTooltip>
            )}
          </div>
            <LightTooltip title='View History' placement='top'>
              <HistoryIcon
                onClick={(event) => {
                  if (event.ctrlKey) {
                    window.open(
                      window.location.origin +
                        window.location.pathname.replace(
                          "invites",
                          `history/?candidateId=${
                            row && row.original && row.original.candidateId
                          }`
                        )
                    );
                  } else {
                    this.viewHistory(row.original);
                  }
                }}
                style={{
                  width: "18px",
                  marginLeft: "10px",
                  cursor: "pointer",
                  color: "#04724d",
                }}
              />
            </LightTooltip>
          </div>
        ),
      },

      {
        Header: () => <div style={{ textAlign: "center" }}>Email</div>,
        minWidth: 200,
        accessor: "email",
        style: { whiteSpace: "initial" },
        className :'left-only',
        Cell: (row) => (
          <div
            style={{
              display: "flex",
            }}
          >
            {row.original.jdCount > 1 ? (
              <LightTooltip
                title={`This email appeared across ${
                  row.original && row.original.jdCount
                    ? row.original.jdCount
                    : 0
                } JobTitles and ${
                  row.original && row.original.scheduleCount
                    ? row.original.scheduleCount
                    : 0
                } schedules`}
                placement='top'
              >
                {/* <div className='count-div'>{row.original.jdCount}</div> */}
                <div className='count-div-color'>{row.value}</div>
              </LightTooltip>
            ) : (
              <div>{row.value}</div>
            )}
          </div>
        ),
      },

      {
        Header: () => <div style={{ textAlign: "center" }}>Phone no</div>,
        maxWidth: 200,
        accessor: "phone",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Track Status</div>,
        maxWidth: 200,
        accessor: "trackStatus",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            onClick={() => this.HistoryPage(row && row.original)}
            style={{
              textAlign: "center",
              color: "#04724d",
              fontWeight: "600",
              cursor: "pointer",
            }}
          >
            {row.value[0].toUpperCase() + row.value.slice(1).toLowerCase()}
            <ArrowRightIcon
              style={{
                color: "#04724d",
              }}
            />
          </div>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Interaction</div>,
        maxWidth: 200,
        accessor: "interaction",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Chat</div>,
        width: 100,
        accessor: "chat",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              alignItems: "center",
            }}
          >
            <div className='chat-image'>
              <img
                alt=''
                src={GmailIcon}
                onClick={() => {
                  this.setState({
                    emailPopup: true,
                    currentEmail: [row.original.email],
                    inviteId: row.original.id,
                  });
                }}
              />
            </div>
            <div className='chat-image'>
              <img
                alt=''
                src={SmsIcon}
                onClick={() => {
                  this.setState({
                    smsPopup: true,
                    currentPhone: row.original.phone,
                    inviteId: row.original.id,
                  });
                }}
              />
            </div>
            {row.original.whatsappEnabled ? (
              <div className='chat-image'>
                {row && row.original && row.original.whatsappSeen === false ? (
                  <span className='markerSeen'></span>
                ) : null}
                <img
                  alt=''
                  src={WhatsappIcon}
                  onClick={() => {
                    this.props.ScheduleAction.inviteHistory({
                      inviteId: row.original.id,
                      candidateId: row.original.candidateId,
                    });
                    row &&
                      row.original &&
                      row.original.whatsappSeen === false &&
                      this.props.ScheduleAction.whatsappSeen({
                        medium: "whatsapp",
                        inviteId: row.original.id,
                      });
                    this.setState({
                      currentPhone: row.original.phone,
                      inviteId: row.original.id,
                    });
                  }}
                />
              </div>
            ) : null}
          </div>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Device</div>,
        maxWidth: 200,
        accessor: "device",
        style: { whiteSpace: "initial" },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Reminder</div>,
        width: 100,
        accessor: "reminderCount",
        style: { textAlign:'left' },
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Reschedule</div>,
        minWidth: 100,
        accessor: "rescheduleCount",
        style: { textAlign:'left' },
      },
    ];
    const customStyles = {
      control: (base) => ({
        ...base,
        height: 34,
        minHeight: 34,
        maxHeight: 34,
        padding: 0,
        borderRadius: 4,
      }),
    };
    return (
      <>
        <Navbar label={this.state.label} />
        <div className='main-div-invites'>
          <div className='top-wrapper-invites'>
            <div className='search-input-invites'>
              <SearchInput
                className='invitesSearch'
                imageOnClick={() => {}}
                placeholder='Search'
                value={search}
                onKeyPress={(e) => this.handleKeyPress(e)}
                onChange={(e) => {
                  this.handleSearch(e);
                }}
                imageClass='invites-img-icon'
              />
              <div className='invitessearchicon'>
                <img
                  alt=''
                  src={searchIcon}
                  onClick={() => this.handleSearchIcon(search)}
                />
              </div>
            </div>

            <div className='select-status-invites'>
              <CustomSelect
                options={statusOptions}
                selected={statusSeleted || []}
                placeholder='Select Status'
                handleChange={this.selectStatus}
                clearAllSelected={() =>
                  this.setState({ statusSeleted: [] }, this.reuseApiCall)
                }
              />
            </div>

            <div className='select-status-invites'>
              <SelectInput
                className='isseenFilter'
                onChange={this.selectResponse}
                options={responseOptions}
                value={responseSelected}
                placeholder={
                  responseSelected && responseSelected.label
                    ? responseSelected.label
                    : "Select Response"
                }
              />
            </div>
            <div className='select-status-invites'>
              <SelectInput
                className='isseenFilter'
                onChange={this.selectWhatsapp}
                options={whatsappOptions}
                value={whatsappSelected}
                placeholder={
                  whatsappSelected && whatsappSelected.label
                    ? whatsappSelected.label
                    : "Select Whatsapp"
                }
              />
            </div>
            <div className='select-status-invites'>
              <SelectInput
                className='isseenFilter'
                onChange={this.selectReschedule}
                options={reminderAndRescheeduleOptions}
                value={rescheduleCount}
                placeholder={
                  rescheduleCount && rescheduleCount.label
                    ? rescheduleCount.label
                    : "Select Reschedule"
                }
              />
            </div>
            <div className='select-status-invites'>
              <SelectInput
                className='isseenFilter'
                onChange={this.selectReminder}
                options={reminderAndRescheeduleOptions}
                value={reminderCount}
                placeholder={
                  reminderCount && reminderCount.label
                    ? reminderCount.label
                    : "Select Reminder"
                }
              />
            </div>
          </div>

          <div className='middle-wrapper-invites'>
            <div className='jd-filter'>
              <CustomSelect
                options={jdOptions}
                selected={jdSeleted || []}
                placeholder='Select JD'
                handleChange={this.selectJds}
                clearAllSelected={() =>
                  this.setState({ jdSeleted: [] }, this.reuseApiCall)
                }
              />
            </div>
            <div className='schedule-filter'>
              <CustomSelect
                options={scheduleOptions}
                selected={scheduleSeleted || []}
                placeholder={"Select Schedule"}
                handleChange={(e) => this.selectSchedule(e)}
                clearAllSelected={() =>
                  this.setState({ scheduleSeleted: [] }, this.reuseApiCall)
                }
              />
            </div>
          </div>

          <div className='table-wrapper-invites'>
            <TableComponent
              data={tableValue}
              columns={inboundDatacolumn}
              showPaginationTop={true}
              nopagination={true}
              handleRefresh={this.reuseApiCall}
              pageSize={limit}
              setPageSize={(e) => this.setLimit(e)}
              gotoPage={(e) => this.handlePagination(e)}
              totalPage={totalPage}
              pageNumber={page ? page : 1}
              handlePrev={this.handleBackPagination}
              handleNext={this.handleNextPagination}
              paginationButtons={[
                // <Button
                //   backgroundColor='#04724D'
                //   color='#ffffff'
                //   width='170px'
                //   height='34px'
                //   fontWeight='bold'
                //   className='sendButton'
                //   fontSize='11px'
                //   margin='0px'
                //   onClick={this.sendViaEmail}
                // >
                //   Send Email
                //   <MailIcon style={{ marginLeft: "4px", fontSize: "medium" }} />
                // </Button>,
                // <Button
                //   backgroundColor='#04724D'
                //   color='#ffffff'
                //   width='170px'
                //   height='34px'
                //   fontWeight='bold'
                //   className='sendButton'
                //   fontSize='11px'
                //   onClick={this.sendViaWhatsapp}
                // >
                //   Send Whatsapp
                //   <WhatsAppIcon
                //     style={{ marginLeft: "4px", fontSize: "medium" }}
                //   />
                // </Button>,
                <SelectInput
                  className='ActionsButton'
                  iconWhite={true}
                  onChange={this.moreActionHandler}
                  options={moreActionMulti}
                  value={acionMultiSelected}
                  placeholder={
                    acionMultiSelected ? acionMultiSelected : "More Actions"
                  }
                  background={"#04724D"}
                  color={"#fff"}
                  fontSize={"12px"}
                />,
                <div style={{ width: "250px", margin: "0 5px" }}>
                  <SelectInput
                    // className='isseenFilter'
                    onChange={this.sendViaWhatsapp}
                    options={templateOptions}
                    value={templateSelected}
                    placeholder={
                      templateSelected && templateSelected.label
                        ? templateSelected.label
                        : "Select Template For WhatsApp"
                    }
                  />
                </div>,
                // <Button
                //   backgroundColor='#04724D'
                //   color='#ffffff'
                //   width='170px'
                //   height='34px'
                //   fontWeight='bold'
                //   className='sendButton'
                //   fontSize='11px'
                //   onClick={this.sendViaSMS}
                //   margin='0px'
                // >
                //   Send SMS
                //   <MessageIcon
                //     style={{ marginLeft: "4px", fontSize: "medium" }}
                //   />
                // </Button>,
              ]}
              selectedRows={seledtedRowId.length}
              message={seledtedRowId.length ? `` : ""}
            />

            {/* {this.state.whatsappPopup ? (
              <MessagePopup
                color="#fff"
                background="#04724d"
                currentPhone={currentPhone}
                closePopUpHandler={() => {
                  this.setState({ whatsappPopup: false });
                }}
                currentMessage={currentMessage}
                currentMessageHandler={(e) => {
                  this.setState({ currentMessage: e.target.value });
                }}
                sendWhatsapp={this.sendWhatsapp}
              />
            ) : null} */}

            {this.state.emailPopup ? (
              <EmailPopup
                popupCloseHandler={() => {
                  this.setState({ emailPopup: false });
                }}
                currentEmail={currentEmail}
                currentMessage={currentMessage}
                onChangeCurrentEmail={this.onChangeCurrentEmail}
                onChangeMessage={(e) => {
                  this.setState({ currentMessage: e });
                }}
                renderValues={this.renderValues}
                emailSubject={emailSubject}
                subjectChangeHandler={(e) => {
                  this.setState({ emailSubject: e.target.value });
                }}
                sendEmail={this.sendEmail}
              />
            ) : null}

            {this.state.smsPopup ? (
              <MessagePopup
                background='#fff'
                color='#04724d'
                currentPhone={currentPhone}
                closePopUpHandler={() => {
                  this.setState({ smsPopup: false });
                }}
                currentMessage={currentMessage}
                currentMessageHandler={(e) => {
                  this.setState({ currentMessage: e.target.value });
                }}
                sendWhatsapp={this.sendSMS}
                sms={true}
              />
            ) : null}
          </div>
          {this.state.whatsappPopup ? (
            <Modal
              open={this.state.whatsappPopup}
              onClose={() =>
                this.setState({ whatsappPopup: false }, this.reuseApiCall())
              }
            >
              <Card className='WhatsAppModel'>
                <CardContent className='AreaComment'>
                  <div className='headerComment'>
                    <div className='harderArea'>
                      <img src={WhatsappIcon} alt='' />
                      <div>Message To {currentPhone}</div>
                    </div>
                    <img
                      src={CloseCommentIcon}
                      alt='close'
                      onClick={() =>
                        this.setState(
                          { whatsappPopup: false },
                          this.reuseApiCall()
                        )
                      }
                    />
                  </div>
                  <div className='OldCommentSection' id='#commentListSection'>
                    {this.state.whatsappEvents.map((item) => {
                      let today = new Date(item.timestamp);
                      let date =
                        today.getFullYear() +
                        "-" +
                        (today.getMonth() + 1) +
                        "-" +
                        today.getDate();
                      let time =
                        today.getHours() +
                        ":" +
                        today.getMinutes() +
                        ":" +
                        today.getSeconds();
                      return item.event == "whatsapp sent" ? (
                        <div className='oldcommentText right'>
                          <div className='timeComment'>
                            {date} {time}
                          </div>
                          <div className='noteComment'>{item.message}</div>
                        </div>
                      ) : (
                        <div className='oldcommentText left'>
                          <div className='timeComment'>
                            {date} {time}
                          </div>
                          <div className='noteComment'>{item.message}</div>
                        </div>
                      );
                    })}
                  </div>
                  <div className='NewCommentSection'>
                    {/* <div className="CommentText"> */}
                    <textarea
                      rows='3'
                      className='commentTextArea'
                      placeholder={
                        currentMessage ? currentMessage : "Enter Message"
                      }
                      onChange={(e) => {
                        this.setState({ currentMessage: e.target.value });
                      }}
                      value={currentMessage ? currentMessage : ""}
                    ></textarea>
                    {/* </div> */}
                    <div className='sendComment'>
                      <Button
                        backgroundColor='#04724D'
                        color='#ffffff'
                        width='100px'
                        height='34px'
                        fontWeight='bold'
                        className='sendButtonComment'
                        fontSize='11px'
                        disabled={!currentMessage}
                        onClick={this.sendWhatsapp}
                      >
                        Send
                        <SendIcon
                          style={{
                            fontSize: "medium",
                            color: "#fff",
                            marginLeft: "20px",
                          }}
                        />
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </Modal>
          ) : null}
          <Modal
            open={this.state.whatsappTemplateModal}
            className='whatsAppTemplateModal'
            onClose={() => this.setState({ whatsappTemplateModal: false })}
          >
            <div className='TemplateMain'>
              <div className='TemplateHeader'>
                <div>WhatsApp Template Message</div>
                <img
                  src={CloseCommentIcon}
                  alt='close'
                  onClick={() =>
                    this.setState({ whatsappTemplateModal: false })
                  }
                />
              </div>
              <div className='TemplateBody'>
                <div className='TemplateContent'>
                  {whatsAppTemplate &&
                  whatsAppTemplate.header &&
                  whatsAppTemplate.header.headerOriginal ? (
                    <div>
                      <div className='ContentHeader'>Title</div>
                      <div className='ContentTitle'>
                        {whatsAppTemplate.header.headerOriginal}
                      </div>
                      <div className='line'></div>
                    </div>
                  ) : null}
                  <div className='ContentHeader'>Body</div>
                  <div className='ContentBody'>
                    <pre>
                      {whatsAppTemplate && whatsAppTemplate.bodyOriginal}
                    </pre>
                  </div>
                  <div className='line'></div>
                  <div className='ContentHeader'>Sample Content</div>
                  <div className='ContentInput'>
                    {templateCustom &&
                      templateCustom.length > 0 &&
                      templateCustom.map((el) => {
                        const val =
                          profile &&
                          profile.find(
                            (item) => item[el && el.paramName.toLowerCase()]
                          );

                        return (
                          <div>
                            <div className='LabelText'>
                              {el && el.paramName}
                            </div>
                            <CustomizedInputs
                              className={"templateInput"}
                              disabled={
                                el && el.paramName.toLowerCase() === "name"
                              }
                              placeholder={el && el.paramName}
                              value={
                                el && el.paramName.toLowerCase() === "name"
                                  ? selectedEmailPhone &&
                                    selectedEmailPhone.length > 0 &&
                                    selectedEmailPhone[0] &&
                                    selectedEmailPhone[0].name
                                  : val
                                  ? val[el && el.paramName.toLowerCase()]
                                  : ""
                              }
                              onChange={
                                el && el.paramName.toLowerCase() === "name"
                                  ? () => null
                                  : (e) => this.profileData(e, el)
                              }
                            />
                          </div>
                        );
                      })}
                  </div>
                </div>
                <div className='TemplatePreview'>
                  <div className='preview-heading'>Preview</div>
                  <div className='preview-card'>{preview}</div>
                  {whatsAppTemplate &&
                  whatsAppTemplate.buttons &&
                  whatsAppTemplate.buttons.length > 0
                    ? whatsAppTemplate.buttons.map((el) => {
                        return (
                          <div className='preview-button'>
                            {el && el.parameter && el.parameter.text}
                          </div>
                        );
                      })
                    : null}
                </div>
              </div>
              <div className='TemplateFooter'>
                <Button
                  backgroundColor='#04724D'
                  color='#ffffff'
                  width='80px'
                  height='34px'
                  fontWeight='bold'
                  className='sendButtonTemplate'
                  fontSize='11px'
                  onClick={this.sendTemplate}
                >
                  Send
                  <SendIcon
                    style={{
                      fontSize: "medium",
                      color: "#fff",
                      marginLeft: "20px",
                    }}
                  />
                </Button>
              </div>
            </div>
          </Modal>
          <Modal
            open={this.state.rescheduleModal}
            className='RescheduleModal'
            onClose={() => this.setState({ rescheduleModal: false })}
          >
            <div className='MainReschedule'>
              <div className='TitleReschedule'>
                <div>Reschedule</div>
                <img
                  src={CloseCommentIcon}
                  alt='close'
                  onClick={() => this.setState({ rescheduleModal: false })}
                />
              </div>
              <div className='rescheduleBody'>
                <div className='BodyOptions'>
                  <DatePicker
                    label='Start Date'
                    className='datePickerReschedule'
                    format='dd - MMM - yyyy'
                    name='datepicker'
                    isNoPast={true}
                    value={this.state.startDateReschedule}
                    onChange={(date) =>
                      this.setState({ startDateReschedule: date })
                    }
                  />
                  <TimePicker
                    className='timePickerReschedule'
                    id='Start-time-picker'
                    label='Start Time'
                    name='timefrom'
                    value={this.state.timefrom}
                    onChange={(date) => {
                      this.setState({ timefrom: date });
                    }}
                  />
                  <DatePicker
                    label='End Date'
                    className='datePickerReschedule'
                    format='dd - MMM - yyyy'
                    name='datepicker'
                    isNoPast={true}
                    value={this.state.endDateReschedule}
                    min={this.state.startDateReschedule}
                    onChange={(date) =>
                      this.setState({ endDateReschedule: date })
                    }
                  />
                  <TimePicker
                    id='End-time-picker'
                    className='timePickerReschedule'
                    label='End Time'
                    name='timeTo'
                    value={this.state.timeTo}
                    onChange={(date) => this.setState({ timeTo: date })}
                  />
                  <div className='TimeZoneReschedule'>
                    <div className='text-reschedule'>Time Zone</div>{" "}
                    <TimezoneSelect
                      className='TimeZoneContainer'
                      value={this.state.timeZone}
                      onChange={(e) => {
                        this.setState({
                          timeZone: e,
                        });
                      }}
                      styles={customStyles}
                    />
                    {rescheduleClicked && this.state.timeZone === "" && (
                      <div className='error'>Please select time zone</div>
                    )}
                  </div>
                  <div className='TimeZoneReschedule'>
                    <div className='text-reschedule'>Allowed Devices</div>
                    <SelectInput
                      className='TimeZoneContainer'
                      onChange={(e) => {
                        this.setState({ allowedDevices: e.value });
                      }}
                      options={this.state.allowedDevicesOptions}
                      placeholder={
                        this.state.allowedDevices
                          ? this.state.allowedDevices
                          : "Select Device"
                      }
                    />
                  </div>
                </div>
                <div>
                  <div className='text-reschedule'>Message</div>
                  <div className='EditorReschedule'>
                    <JoditEditor
                      ref={this.setStateeditor}
                      value={this.state.content}
                      config={config}
                      key={9}
                      onBlur={(newContent) => this.handleUpdate(newContent)}
                      onChange={(newContent) => this.handleUpdate(newContent)}
                    />
                    <div
                      style={{ display: "none" }}
                      dangerouslySetInnerHTML={{ __html: this.state.content }}
                    />
                  </div>
                  {rescheduleClicked && this.state.content === "" && (
                    <div className='error'>Please enter message</div>
                  )}
                </div>
              </div>
              <div className='RescheduleFooter'>
                <Button
                  backgroundColor='#04724D'
                  color='#ffffff'
                  width='130px'
                  height='34px'
                  fontWeight='bold'
                  className='sendButtonReschedule'
                  fontSize='11px'
                  onClick={this.rescheduleHandler}
                >
                  Reschedule
                  <SendIcon
                    style={{
                      fontSize: "medium",
                      color: "#fff",
                      marginLeft: "20px",
                    }}
                  />
                </Button>
              </div>
            </div>
          </Modal>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    ScheduleReducer: state.ScheduleReducer,
    JDReducer: state.JobDescriptionReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    ScheduleAction: bindActionCreators(ScheduleActions, dispatch),
    JDAction: bindActionCreators(JDActions, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Invites));
